import React, { useEffect, useState } from 'react';
import './styles/Home.css';
import './styles/Mobile.css';
import { Link } from 'react-router-dom';

function Settings() {
    const [feelings, setFeelings] = useState([]);
    const [editingIndex, setEditingIndex] = useState(null);
    const [modifiedFeelings, setModifiedFeelings] = useState({});
    const [selectedOption, setSelectedOption] = useState(''); // Ajout pour grer le thme

    // Fonction pour grer le changement dans la liste droulante
    const handleSelectionChange = (e) => {
        setSelectedOption(e.target.value);

        // Appliquer la classe correspondante pour changer le thme
        document.body.className = e.target.value; // Applique la classe choisie sur le body
    };

    useEffect(() => {
        fetch('/getFeelings')
            .then(res => res.json())
            .then(data => setFeelings(data.feelings))
            .catch(err => console.error("Erreur en rcuprant les motions :", err));
    }, []);

    const handleInputChange = (index, value) => {
        setModifiedFeelings(prev => ({
            ...prev,
            [index]: value
        }));
    };

    const updateFeeling = (index) => {
        const newFeeling = modifiedFeelings[index];
        if (!newFeeling) return;

        fetch('/updateFeeling', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ index, newFeeling })
        })
        .then(res => res.json())
        .then(data => {
            if (data.success) {
                const updatedFeelings = [...feelings];
                updatedFeelings[index] = newFeeling;
                setFeelings(updatedFeelings);
                setEditingIndex(null);
                setModifiedFeelings({});
            } else {
                console.error("Erreur ct serveur :", data.message);
            }
        })
        .catch(err => console.error("Erreur en mettant  jour :", err));
    };

    return (
        <div className="container">
            <div className="header">
                <h1>
                    <span>M</span><span>y</span><span>D</span><span>a</span><span>y</span>
                </h1>
            </div>
            
            <h5>Theme : </h5>
            <select id="theme-select" class="theme-select">
            <option value="colorful">Color</option>
            <option value="dark">not dev</option>

            </select>

            <h5>Emotions :</h5>
            <ol>
                {feelings.map((f, index) => (
                    <ol key={index}>
                        {editingIndex === index ? (
                            <>
                                <input
                                    type="text"
                                    defaultValue={f}
                                    onChange={(e) => handleInputChange(index, e.target.value)}
                                    
                                />
                                
                                <button 
                                    className="button-option"
                                    onClick={() => updateFeeling(index)}></button>
                                <button 
                                    className="button-option"
                                    onClick={() => setEditingIndex(null)}></button>
                               
                            </>
                        ) : (
                            <>
                                {f}
                                <button 
                                    className="button-option"
                                    onClick={() => setEditingIndex(index)}></button>
                            </>
                        )}
                         {index < feelings.length - 1 && <hr className="hr-settings" />}
                    </ol>
                    
                ))}
            </ol>

            <Link to="/">
                <button type="button" className="submit-button">Mes motions</button>
            </Link>

            <p className="droits">
                 2025 myDay. Tous droits rservs. Cette application, ainsi que lensemble de son contenu, est protge par les lois en vigueur relatives  la proprit intellectuelle. Les donnes quelle contient sont chiffres afin den garantir la scurit.
            </p>
        </div>
    );
}

export default Settings;
